body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --supportcolor:white;
  --suppdisp:none;
  --suppinfocol:white;
  --supptextcol:black;
  --navdisp:none;
}
.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
#navbtn{
  
  display:none;
  
  display:var(--navdisp);
}
#navbtn:hover{
  color:#42bd45;
  transition: ease all 0.2s !important;
}
#helpbtn:hover{transition: ease all 0.2s !important;
color:#42bd45;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(transparent, #42bd45);
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(transparent, #00c6ff);
}

.loader{z-index: 100000;
  position: fixed;

width: 100%;
  
}

.suppicon{cursor: pointer;
  color:white;
  color:var(--supportcolor);
}

.suppicon:hover{transition: ease all 0.5s;
  transform:scale(1.15);
}

.suppinfo{transition: ease all 4s;
  z-index: 1000000000;
display: none;
display: var(--suppdisp);
  background-color: white;
  background-color: var(--suppinfocol);
  padding: 15px;
  bottom:0;
  margin-bottom: 6.1%;
  margin-right:1.4%;
  border-radius: 8px;
  transform: display 1s;
  
}

.supptext{
  color:black;
  color:var(--supptextcol);
}

