  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #42bd45);
}

.linkcontents:hover span:nth-child(2) {
  top: 100%;
  transition: 1s;
  transition-delay: 0.25s;
}

.linkcontents span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #42bd45);
}

.linkcontents:hover span:nth-child(4) {
  bottom: 100%;
  transition: 1s;
  transition-delay: 0.75s;
}


